import './App.css';
import React, { Component } from 'react';
import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import { Map, GoogleApiWrapper, Marker, InfoWindow, Polyline } from 'google-maps-react';
import { isMobileDevice } from './commons/device';


export class MapContainer extends Component {
  constructor(props) {
    super(props);
    this.firestore = null;
    var bd = new Date();
    bd.setHours(0, 0, 0);
    this.beginningDateObject = bd;
    try {
      let current = firebase.auth().currentUser;
      if (!current) {
        this.props.replace('/', '');
      }
      this.firestore = firebase.firestore();
    } catch (error) {
      this.props.history.replace('/', '');
    }
    this.state = {
      stores: [],
      locations: [],
      selectedBusId: -1,
      buses: [
        { id: 'Cp3arit19GVGsKMVhxew', name: 'Bus 1' }, {
          id: 'PcSUAY7w4p3nXfP9MLyf', name: 'Bus 2'
        }, {
          id: 'ql9zKGWuOZtCRJncfaIL', name: 'Bus 3'
        }, {
          id: 'test', name: 'TEST'
        }
      ],
      selectedPlace: '',
      activeMarker: null,
      showingInfoWindow: false,
      distance: '--',
      selectedBus: null,
      dates: [],
      selectedDate: 0
    }
  }

  componentDidMount() {
    var dates = [];

    for (var i = 0; i < 5; i++) {
      var date = new Date();
      date.setDate(this.beginningDateObject.getDate() - i);
      date.setHours(0,0,1);
      dates.push(date);
    }
    this.setState({dates});
  }

  displayMarkers = () => {
    return this.state.stores.map((store, index) => {
      return <Marker key={index} id={index} title='Hi' name={store.time} position={{
        lat: store.latitude,
        lng: store.longitude
      }}
        onClick={this.onMarkerClick}  >
      </Marker>
    })
  }

  deleteOldDates(bus) {
    let batch = this.firestore.batch();
    this.firestore.collection('server').doc(bus.id).collection('tracking').where('CreatedAt', '<=', this.state.dates[4]).orderBy('CreatedAt').get().then(
      querySnapshot => {
        querySnapshot.forEach(
          element => {
            batch.delete(element.ref);
          }
        );
        batch.commit().then(data => { });
      });
  }

  getMarkers(bus) {
    this.setState({ selectedBusId: bus.id });
    let markers = [];
    const bounds = new window.google.maps.LatLngBounds();
    let locations = [];
    this.deleteOldDates(bus);
    let distance = 0.0;
    var endDate = new Date();
    endDate.setDate(this.state.dates[this.state.selectedDate].getDate());
    endDate.setHours(23,59,59,999); 
    this.firestore.collection('server').doc(bus.id).collection('tracking').where('CreatedAt', '>', this.state.dates[this.state.selectedDate]).where('CreatedAt', '<', endDate).orderBy('CreatedAt').get().then(
      querySnapshot => {
        querySnapshot.forEach(
          element => {
            distance += .025;
            let marker = element.data();
            console.log(marker);
            let localDate = new Date(0); // The 0 there is the key, which sets the date to the epoch
            localDate.setUTCSeconds(marker.CreatedAt.seconds);
            let time = `${localDate.getHours()}:${localDate.getMinutes()}`
            markers.push({ latitude: marker.latitud, longitude: marker.longitud, time });
            bounds.extend(new window.google.maps.LatLng(
              marker.latitud,
              marker.longitud
            ));
            locations.push({ lat: marker.latitud, lng: marker.longitud });
          }
        );
        this.setState({ stores: markers, locations: locations, distance: distance.toString() }, () => {

        });
      }
    );
  }

  onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    handleDateSelection = (event) =>
    this.setState({selectedDate: event.target.value});

  render() {
    return (
      <div>
        <div className="navbar navbar-dark bg-dark" style={{ display: 'flex', paddingTop: 20, alignItems: 'center', paddingBottom: 20 }} >
          <img src={'/admin-app/assets/Nogales.png'} height={isMobileDevice() ? 30 : '100%'} alt="Logo"></img>
          <div onClick={() => {
            firebase.auth().signOut().then(
              () => {
                this.props.history.replace('/', '');
              }
            )
          }} className="btn btn-outline-success my-2 my-sm-0">Salir</div>
        </div>

        <div className="container mt-3">
          <div className="row">
            <div className="col-md-3">
              <div className="card p-3">
                <h4 className="mb-3">Camiones de basura</h4>
                <br></br>
                <select className="form-group" value={this.state.selectedDate} onChange={this.handleDateSelection}>
                  { this.state.dates ?  this.state.dates.map( (res, index) => <option value={index}>
                    {`${res.getDate()}-${res.getMonth()+1}-${res.getFullYear()}`}</option>):null}
                </select>

                <br></br>
                <h2>Reporte del día</h2>
                <div className="list-group">
                  {this.state.buses.map(bus => <div className={bus.id === this.state.selectedBusId ? 'list-group-item active' : 'list-group-item'} style={{ cursor: "pointer" }} onClick={() => this.getMarkers(bus)} >{bus.name}</div>)}
                </div>

                <div className="mt-3 p-3 card">
                  <strong className="text-center">Distancia recorrida</strong>
                  <strong className="mt-3 text-center">{this.state.distance} KM</strong>
                </div>
              </div>
            </div>
            <div className="col-md-9">

              <Map
                google={this.props.google}
                zoom={14}
                style={{
                  width: '100%',
                  height: 500,
                  overFlow: 'hidden',
                  marginRight: 0
                }}
                ref={ref => this.map = ref}
                initialCenter={{ lat: 18.8157483, lng: -97.1640488 }}
              >
                {this.displayMarkers()}
                <InfoWindow
                  marker={this.state.activeMarker}
                  visible={this.state.showingInfoWindow}>
                  <div>
                    <p>{this.state.selectedPlace.name}</p>
                  </div>
                </InfoWindow>
                {this.state.locations ? <Polyline
                  path={this.state.locations}
                  strokeColor="#0000FF"
                  strokeOpacity={0.8}
                  strokeWeight={2} /> : null}
              </Map>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

MapContainer = GoogleApiWrapper({
  apiKey: 'AIzaSyCls2n588WAf6AHLWYU69MKcPWKYrMfJcI',
  language: "ES"
})(MapContainer);
