import React, { Component } from 'react';
import firebase from 'firebase/app';
import 'firebase/auth';
import { isMobileDevice } from '../commons/device';

class LoginComponent extends Component {

    constructor(props) {
        super(props);
        if (!firebase.apps.length) {
            this.app = firebase.initializeApp({
                apiKey: "AIzaSyBkRMV8HV4cazNztL2t5KAQ-QJKw1ch_Ug",
                authDomain: "nogalesapp-1.firebaseapp.com",
                databaseURL: "https://nogalesapp-1.firebaseio.com",
                projectId: "nogalesapp-1",
                storageBucket: "nogalesapp-1.appspot.com",
                messagingSenderId: "235008508187",
                appId: "1:235008508187:web:44833208e4bb535479d3be"
            });
        }
        this.state = {
            fetching: false,
            error: '',
            email: '',
            password: ''
        }
    }

    componentDidMount() {
    }

    componentWillUnmount() {

    }
    login = (e) => {
        e.preventDefault();
        let err_message = 'Realizando Login ...';
        this.setState({ fetching: true, error: err_message });
        firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password).then(
            data => { this.props.history.push('/map'); },
            err => {
                err_message = err.code === 'auth/user-not-found' ? 'Usuario no encontrado' : 'Contraseña incorrecta';
                this.setState({ fetching: false, error: err_message });
            }
        )
    }


    render() {
        return (
            <div>
                <div className="navbar navbar-dark bg-dark" style={{ display: 'flex', justifyContent: isMobileDevice() ? '' : 'center', paddingTop: 20, paddingBottom: 20 }} >
                    <img src={window.location.origin + '/admin-app/assets/Nogales.png'} height={isMobileDevice() ? 30 : '100%'} className="img-responsive" alt="Logo"></img>
                </div>
                <div className="mt-3 pt-3" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                    <b className="mb-4">Para ingresar al sistema, inicie sesión.</b>
                    <hr></hr>
                    <form onSubmit={this.login} style={{ width: '50%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                        <div className="form-group" style={{ width: '100%' }} >
                            <input value={this.state.email} onChange={evt => this.setState({ email: evt.target.value })} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Ingrese correo electrónico" required></input>
                        </div>
                        <div className="form-group" style={{ width: '100%' }}>
                            <input value={this.state.password} onChange={evt => this.setState({ password: evt.target.value })} type="password" className="form-control" id="exampleInputPassword1" placeholder="Contraseña" required></input>
                        </div>
                        <button onClick={this.login} type="submit" className="btn btn-secondary btn-lg btn-block">Ingresar</button>
                        <p className='mt-3' style={{ alignSelf: 'flex-end', color: this.state.fetching ? 'black' : 'red' }} >{this.state.error}</p>
                    </form>

                </div>
            </div>
        );
    }
}

export { LoginComponent };
